}
@-webkit-keyframes invisiblyGrowFontSize {
0% {
    font-size: 0;
    opacity: 0;
}
100% {
    font-size: 1em;
    opacity: 0;
}
}
@keyframes invisiblyGrowFontSize {
0% {
    font-size: 0;
    opacity: 0;
}
100% {
    font-size: 1em;
    opacity: 0;
}
}
@-webkit-keyframes fadeIn {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@keyframes fadeIn {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
.detailsContainer {
  -webkit-transition: max-height 0.7s ease-out;
  transition: max-height 0.7s ease-out;
  -webkit-transform-origin: top;
          transform-origin: top;
  text-align: left;
  overflow-x: hidden;
  overflow-y: scroll;
}
.detailsContainer:not([open]) {
  max-height: 100px;
}
.detailsContainer[open] {
  max-height: 600px;
}
.detailsContainer[open] .detailsContent {
  -webkit-animation-name: invisiblyGrowFontSize, fadeIn;
          animation-name: invisiblyGrowFontSize, fadeIn;
  -webkit-animation-duration: 700ms, 500ms;
          animation-duration: 700ms, 500ms;
  -webkit-animation-delay: 0ms, 200ms;
          animation-delay: 0ms, 200ms;
}
.detailsSummary {
  margin: 5px;
  outline:none;
}
.boxshadow {
  -webkit-box-shadow: 2px 2px 2px grey;
          box-shadow: 2px 2px 2px grey;
}
.boxborder {
  border-radius: 10px;
  border: 1px solid rgba(0, 0, 0, 0.1);
}
.textBig {
  font-size: 20px;
  font-weight: bolder;
}
.textMedium {
  font-size: 18px;
  font-weight: normal;
}
.textSmall {
  font-size: 15px;
  font-weight: normal;
}
.textInput {
  margin: 5px;
  padding: 3px;
  padding-left: 8px;
  float: left;
  outline: none;
  background-color: rgba(0, 0, 0, 0.1);
}
.switch {
  position: relative;
  /*display: inline-block;*/
  float: right;
  width: 50px;
  height: 27px;
  margin: 6.5px;
}
.switch input {display:none;
}
.sliderSwitch {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 27px;
  background-color: rgba(0, 0, 0, 0.1);
  -webkit-transition: .4s;
  transition: .4s;
}
.sliderSwitch:before {
  position: absolute;
  content: "";
  height: 23px;
  width: 23px;
  left: 2px;
  bottom: 2px;
  border-radius: 50%;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}
input:checked + .sliderSwitch {
  background-color: #2196F3;
}
input:focus + .sliderSwitch {
  -webkit-box-shadow: 0 0 1px #2196F3;
          box-shadow: 0 0 1px #2196F3;
}
input:checked + .sliderSwitch:before {
  -webkit-transform: translateX(23px);
  transform: translateX(23px);
}
hr {
  width: 100%;
  color: rgba(0, 0, 0, 0.5);
}
.buttonCircle {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  text-align: center;
